import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'DBX',
  description:
    'The most popular AMM on BSC by user count! Earn CAKE through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by DBX), NFTs, and more, on a platform you can trust.',
  image: 'https://pancakeswap.finance/images/hero.png',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else if (path.startsWith('/nfts/collections')) {
    basePath = '/nfts/collections'
  } else if (path.startsWith('/nfts/profile')) {
    basePath = '/nfts/profile'
  } else if (path.startsWith('/pancake-squad')) {
    basePath = '/pancake-squad'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('DBX')}`,
      }
    case '/swap':
      return {
        title: `${t('Exchange')} | ${t('DBX')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('DBX')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('DBX')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('DBX')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('DBX')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('DBX')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('DBX')}`,
      }
    case '/prediction/leaderboard':
      return {
        title: `${t('Leaderboard')} | ${t('DBX')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('DBX')}`,
      }
    case '/farms/auction':
      return {
        title: `${t('Farm Auctions')} | ${t('DBX')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('DBX')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('DBX')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('DBX')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('DBX')}`,
      }
    case '/voting':
      return {
        title: `${t('Voting')} | ${t('DBX')}`,
      }
    case '/voting/proposal':
      return {
        title: `${t('Proposals')} | ${t('DBX')}`,
      }
    case '/voting/proposal/create':
      return {
        title: `${t('Make a Proposal')} | ${t('DBX')}`,
      }
    case '/info':
      return {
        title: `${t('Overview')} | ${t('DBX Info & Analytics')}`,
        description: 'View statistics for DBX exchanges.',
      }
    case '/info/pools':
      return {
        title: `${t('Pools')} | ${t('DBX Info & Analytics')}`,
        description: 'View statistics for DBX exchanges.',
      }
    case '/info/tokens':
      return {
        title: `${t('Tokens')} | ${t('DBX Info & Analytics')}`,
        description: 'View statistics for DBX exchanges.',
      }
    case '/nfts':
      return {
        title: `${t('Overview')} | ${t('DBX')}`,
      }
    case '/nfts/collections':
      return {
        title: `${t('Collections')} | ${t('DBX')}`,
      }
    case '/nfts/profile':
      return {
        title: `${t('Your Profile')} | ${t('DBX')}`,
      }
    case '/pancake-squad':
      return {
        title: `${t('Pancake Squad')} | ${t('DBX')}`,
      }
    default:
      return null
  }
}
